@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&family=Permanent+Marker&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --azulOscuro: #81c9c5;
  --azulClaro: #c9e9e8;
  --amarillo: #f2e2ba;
  --rojo: #fa736a;
  --rojoHover: #f6918a;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/*Damos un padding al header y al footer y los centramos usando grid con place-content que sirve para centrar tanto en el eje vertical como en el horizontal (porque en este caso tanto el header como el footer solo tienen un elemento que ocupa toda una zelda)*/
.app > header {
  display: grid;
  place-content: center;
  padding: 2rem;
  background-color: var(--azulOscuro);
}

/*Damos estilo al h1 que es hijo directo de App*/
.app > header h1 {
  font-size: 3.5rem;
  font-family: 'Permanent Marker', sans-serif;
  border: 4px solid var(--rojo);
  border-radius: 1rem;
  padding: 0px 20px 2px 20px;
  background-color: var(--amarillo);
  box-shadow: 8px 8px 5px 3px rgba(0, 0, 0, 0.15);
  color: var(--rojo);
}

main {
  flex-grow: 1;
  background-color: var(--amarillo);
}

.new-task-form {
  background-color: var(--azulClaro);
  margin: 1rem 20px 20px 20px;
  padding: 1rem;
  border: 2px solid var(--azulOscuro);
  border-radius: 1rem;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.text-input {
  border: 2px solid var(--azulOscuro);
  padding: 0.5rem;
}

.button {
  font-size: 12px;
  font-weight: bold;
  background-color: var(--rojo);
  border-radius: 0.5rem;
  margin-left: 10px;
  border: 2px solid black;
  padding: 0.5rem;
}

.button:hover {
  background-color: var(--rojoHover);
}

.h2-errase-button-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 1rem 0rem;
}

h2 {
  /*align-self: center;*/
  font-size: 1.3rem;
  font-family: 'Permanent Marker', sans-serif;
  color: var(--rojo);
  margin-right: 20px;
}

.errase-button {
  /*align-self: center;*/
  font-size: 12px;
  font-weight: bold;
  background-color: var(--rojo);
  border-radius: 0.5rem;
  border: 2px solid black;
  padding: 0.5rem 0.5rem;
  /*   margin-top: 7px; */
}

.errase-button:hover {
  background-color: var(--rojoHover);
}

.tasks-container {
  background-color: var(--azulClaro);
  margin: 10px 20px 20px 20px;
  padding: 1rem;
  border: 2px solid var(--azulOscuro);
  border-radius: 1rem;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.task-done,
.task-pending {
  list-style: none;
  align-self: flex-start;
  padding: 8px 0px;
}

.tareas {
  margin-left: 2rem;
}

li.task-done {
  text-decoration: line-through;
  color: var(--rojo);
  font-weight: bold;
}

/*Ponemos el texto del footer en negrita*/

.app > footer {
  display: grid;
  place-content: center;
  padding: 1rem;
  background-color: var(--azulOscuro);
}

.app > footer p {
  font-size: 1.2rem;
  font-weight: bold;
  color: var(--rojo);
  font-family: 'Permanent Marker', sans-serif;
  text-shadow: 1px 1px 2px rgb(1, 148, 216), 0 0 1em var(--amarillo),
    0 0 0.2em var(--rojoHover);
}

/*#####################################################################*/

/*
### RESPONSIVE PARA TABLET ##
#############################
#### MEDIA QUERY: 700px #####
#############################
*/
/*Todo esto son cambios específicos que se aplicarán a partir de los 700 píxeles*/
@media (min-width: 700px) {
  .new-task-form {
    border: 3px solid var(--azulOscuro);
    margin: 1rem 100px 20px 100px;
  }

  .text-input {
    border: 3px solid var(--azulOscuro);
    font-size: 20px;
  }

  .h2-errase-button-container {
    padding: 2rem 0rem 1rem 0rem;
  }

  h2 {
    /*align-self: center;*/
    font-size: 2.5rem;
  }

  .button {
    font-size: 20px;
  }

  .errase-button-container {
    margin-left: 40px;
  }

  .errase-button {
    /*align-self: center;*/
    font-size: 20px;
    margin-top: 7px;
  }

  .tasks-container {
    margin: 10px 100px;
    padding: 1rem;
    border: 3px solid var(--azulOscuro);
  }

  .checkbox {
    width: 17px;
    height: 17px;
  }

  .task-done,
  .task-pending {
    font-size: 20px;
    list-style: none;
    align-self: flex-start;
    padding: 8px 0px;
  }

  .tareas {
    margin-left: 2rem;
  }

  li.task-done {
    text-decoration: line-through;
    color: var(--rojo);
    font-weight: bold;
  }

  .app > footer p {
    font-size: 1.5rem;
  }
}

/*#####################################################################*/

/*
##### RESPONSIVE PARA PC ####
#############################
#### MEDIA QUERY: 1200px ####
#############################
*/
/*Todo esto son cambios específicos que se aplicarán a partir de los 1200 píxeles*/
@media (min-width: 1200px) {
  .app > header h1 {
    font-size: 4rem;
  }

  .new-task-form {
    margin: 1rem 20rem;
  }

  .button {
    font-size: 16px;
  }

  .errase-button {
    /*align-self: center;*/
    font-size: 16px;
    margin-top: 9px;
  }

  .tasks-container {
    margin: 10px 20rem;
  }
}
